import React,{useState} from 'react'
import './login.css'
function Login() {
    const [user,setuser]=useState('');
    const handle=(e)=>{
        setuser(e.target.value);
    }
    const display=(e)=>{
        e.preventDefault();
        alert(`welcome ${user} to foodify`);
    }
    
    return (
        <div>
            <form onSubmit={display}>
            <div className="login">
                <h1>Login page</h1>
        
        <input type="e-mail"  value={user} onChange={handle} placeholder="Enter your mail Id" required ></input>
           <input type="password" placeholder="Enter your password" required></input>
           <button type="submit"  className="btn-login"><strong><bold>Login in</bold></strong></button>
        
            </div>
            </form>
            
        </div>
    )
}

export default Login
